/**
 * Site header
 */
.site-header {
  // border-top: 5px solid $grey-color-dark;
  border-bottom: 1px solid $grey-color-light;
  min-height: $spacing-unit * 1.865;

  // Positioning context for the mobile navigation icon
  position: relative;
}

.site-title {
  @include relative-font-size(1.4);
  line-height: $base-line-height * $base-font-size * 2.25;
  letter-spacing: -1px;
  margin-bottom: 0;
  float: left;

  &,
  &:visited {
    color: $grey-color-dark;
  }

  svg {
    vertical-align: middle;
  }
}

.site-nav {
  float: right;
  line-height: $base-line-height * $base-font-size * 2.25;

  .nav-trigger {
      display: none;
  }

  .menu-icon {
    display: none;
  }

  .page-link {
    color: $brand-color;
    line-height: $base-line-height;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  @include media-query($on-palm) {
    position: absolute;
    top: 9px;
    right: $spacing-unit / 2;
    background-color: $background-color;
    border: 1px solid $grey-color-light;
    border-radius: 5px;
    text-align: right;
    z-index: 1;

    label[for="nav-trigger"] {
      display: block;
      float: right;
      width: 36px;
      height: 36px;
      z-index: 2;
      cursor: pointer;
    }

    .menu-icon {
      display: block;
      float: right;
      width: 36px;
      height: 26px;
      line-height: 0;
      padding-top: 10px;
      text-align: center;

      > svg {
        fill: $grey-color-dark;
      }
    }

    input ~ .trigger {
      clear: both;
      display: none;
    }

    input:checked ~ .trigger {
      display: block;
      padding-bottom: 5px;
    }

    .page-link {
      display: block;
      padding: 5px 10px;

      &:not(:last-child) {
        margin-right: 0;
      }
      margin-left: 20px;
    }
  }
}



/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  padding: $spacing-unit 0;
}

.footer-heading {
  @include relative-font-size(1.125);
  margin-bottom: $spacing-unit / 2;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.footer-col-wrapper {
  @include relative-font-size(0.9375);
  color: $grey-color;
  margin-left: -$spacing-unit / 2;
  @extend %clearfix;
}

.footer-col {
  float: left;
  margin-bottom: $spacing-unit / 2;
  padding-left: $spacing-unit / 2;
}

.footer-col-1 {
  width: -webkit-calc(35% - (#{$spacing-unit} / 2));
  width:         calc(35% - (#{$spacing-unit} / 2));
}

.footer-col-2 {
  width: -webkit-calc(20% - (#{$spacing-unit} / 2));
  width:         calc(20% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
  width: -webkit-calc(45% - (#{$spacing-unit} / 2));
  width:         calc(45% - (#{$spacing-unit} / 2));
}

@include media-query($on-laptop) {
  .footer-col-1,
  .footer-col-2 {
    width: -webkit-calc(50% - (#{$spacing-unit} / 2));
    width:         calc(50% - (#{$spacing-unit} / 2));
  }

  .footer-col-3 {
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}

@include media-query($on-palm) {
  .footer-col {
    float: none;
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}


/**
 * Page banner | Custom
 */
.page-banner {
  display: block;
  position: relative;
  height: 640px;
  background-color: #005FCC;
  transition: height 0.2s;
  .page-banner-img {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    > *:first-child {
      height: 100%;
      max-width: 1000%;
      background-size: cover;
      background-position: center center;
      overflow: hidden;
      transition: 0.1s all ease-in-out;
    }
  }
  .page-banner-live {
    position: absolute;
    bottom: 3rem;
    .page-banner-live-title{
      color: #fdfdfd;
      text-shadow: 1px 1px 2px #33333355;
      svg{
        width: 30px;
        height: 30px;
        vertical-align: middle;
      }
    }
    .page-banner-live-list {
      display: flex;
      .card {
        transition: all .3s ease-out;
        display: inline-block;
        width: 250px;
        width: 250px;
        margin-right: 2rem;
        cursor: pointer;
        &:hover{
          transform: translate(0, -7px);
          box-shadow: -1px 20px 80px -30px rgba(0,0,0,0.89);
          -webkit-box-shadow: -1px 20px 80px -30px rgba(0,0,0,0.89);
          .card-body {
            text-decoration: underline;
          }
        }
        .card-img{
          width: 100%;
          height: 180px;
          border-radius: .25rem .25rem 0 0;
        }
        .card-body{
          padding: 1.25rem;
          background-color: #fff;
          border-radius: 0 0 .25rem .25rem;
          h4 {
            color: $brand-color;
            margin-bottom: 5px;
          }
          .desc{
            font-size: 85%;
            font-weight: 300;
            display:-webkit-box; 
            word-wrap:break-word; 
            -webkit-line-clamp:3; 
            -webkit-box-orient:vertical; 
            overflow:hidden; 
            text-overflow:ellipsis;
            height:54px;
          }
          span{
            display: inline-block;
            padding: .4em .4em;
            font-size: 80%;
            line-height: 1;
            border: 1px solid $blue-color !important;
            border-radius: .1rem;
            color: $blue-color;
            transition: background-color .3s ease-out;
            &:hover{
              color: $background-color;
              background-color: $blue-color;
              border-color: $background-color;
            }
          }
        }
      }
    }
  }

}


@include media-query($on-laptop) {
  .page-banner {
    .page-banner-live{
      display: none;
    }
  }
}


/**
 * Page content
 */
.page-content {
  padding: 0 0 $spacing-unit 0;
  flex: 1;

  .home {
    margin-top: 50px;
  }
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
  color: $brand-color;

  svg{
    width: 30px;
    height: 30px;
    vertical-align: middle;
  }
}


/**
 * Side bar | Custom
 */
.framework {
  display: flex;
  .main {
    flex: 1;
  }
  .tags {
    flex: 1;
    .post-preview {
      h2  {
        color: $brand-color;
      }
      ul {
        list-style: none;
        margin: 0;
        margin-bottom: 30px;
        font-size: 90%;
        li {
          margin-bottom: 5px;
        }
      }
    }
  }
  .side-bar {
    margin-left: 15px;
    padding-left: 8px;
    transition: top .2s, display .2s;
    position: sticky;
    position: -webkit-sticky;
    align-self: flex-start;
    transform: scale(0.9999);
    top: 83.84px;
    .side-bar-tags {
      min-width: 200px;
      ul {
        list-style: none;
        margin: 0;
        li {
          border-bottom: 1px solid $grey-color-light;
          transition: background-color .3s ease-out;
          &:hover{
            background-color: $grey-color-light;
          }
          a {
            display: flex;
            justify-content: space-between;
            padding: 8px 12px;
            text-decoration: none;
            color: $grey-color-dark;
            span{
              font-size: 80%;
              display: inline-block;
              border-radius: 10px;
              align-self: center;
              background: $blue-color;
              padding: 0px 8px;
              margin-left: 20px;
              color: $background-color;
            }
          }
        }
      }
    }
  }
}

@include media-query($on-laptop) {
  .framework {
    .side-bar {
      display: none;
    }
  }
}


.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit;
    padding-bottom: 30px;

    &:not(:last-child) {
      border-bottom: 1px solid $grey-color-light;
    }

    > h3 {
      margin-bottom: 0px !important;
    }

    > p {
      margin-top: 15px;
    }

    .tags-list {
      display: flex;
      flex-wrap: wrap;
      list-style-type: none;
      margin-left: 0px;
      li{
        margin-right: 5px;
        margin-bottom: 5px;
        span{
          display: inline-block;
          padding: .4em .4em;
          font-size: 80%;
          line-height: 1;
          border: 1px solid $blue-color !important;
          border-radius: .1rem;
          color: $blue-color;
          transition: background-color .3s ease-out;
          &:hover{
            color: $background-color;
            background-color: $blue-color;
            border-color: $background-color;
          }
        }
      }
    }

  }


}

.post-meta {
  font-size: $small-font-size;
  color: $grey-color;
}

.post-desc {
  font-weight: 300;
  display:-webkit-box; 
  word-wrap:break-word; 
  -webkit-line-clamp:3; 
  -webkit-box-orient:vertical; 
  overflow:hidden; 
  text-overflow:ellipsis;
  height: 66px;
}

.post-link {
  display: block;
  @include relative-font-size(1.5);
}


/**
 * Blog
 */
.blog-header {
  margin-bottom: $spacing-unit;
  padding-top: $spacing-unit * 4;
  padding-bottom: $spacing-unit * 4;
  background-color: #000;

  .post-title {
    color: $background-color;
    text-shadow: 1px 1px 2px #33333355;
  }

  .post-tags {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    margin-left: 0px;
    li{
      margin-right: 5px;
      margin-bottom: 5px;
      span{
        display: inline-block;
        padding: .4em .4em;
        font-size: 80%;
        line-height: 1;
        border: 1px solid $background-color !important;
        border-radius: .1rem;
        color: $background-color;
        transition: background-color .3s ease-out;
        &:hover{
          color: $background-color;
          background-color: $blue-color;
          border-color: $background-color;
        }
      }
    }
  }

  .post-meta {
    color: $background-color !important;
    text-shadow: 1px 1px 2px #33333355;
  }
  .post-author{
    display: flex;
    .thumbnail{
      flex: 1;
      margin: auto;
      > img {
        width: 80px;
        border-radius: 40px;
      }
    }
    .author{
      flex: 9;  
      display: table;
      overflow: hidden;
      height: 80px;
      padding: 10px 10px;
      table-layout: fixed;
      color: $background-color;
      .inner-cell{
        display: table-cell;
        vertical-align: middle;
        .author-name {
          display: block;
          padding-top: 1px;
          font-size: 1.1rem;
          letter-spacing: 0;
          color: $background-color;
          text-shadow: 1px 1px 2px #33333355;
        }
        .author-desc {
          display: block;
          padding-top: 1px;
          font-size: 90%;
          letter-spacing: 0;
          color: $background-color;
          text-shadow: 1px 1px 2px #33333355;
        }
      }
    }
  }
}

/**
 * Posts
 */
.post-header {
  margin-top: $spacing-unit;
  margin-bottom: $spacing-unit;
}


.post-title {
  @include relative-font-size(2.625);
  letter-spacing: -1px;
  line-height: 1;

  @include media-query($on-laptop) {
    @include relative-font-size(2.25);
  }

  svg {
    vertical-align: middle;
  }
}

.post-content {
  margin-bottom: $spacing-unit;

  h1 {
    @include relative-font-size(2.25);

    @include media-query($on-laptop) {
      @include relative-font-size(2);
    }
  }

  h2 {
    @include relative-font-size(2);

    @include media-query($on-laptop) {
      @include relative-font-size(1.75);
    }
  }

  h3 {
    @include relative-font-size(1.625);

    @include media-query($on-laptop) {
      @include relative-font-size(1.375);
    }
  }

  h4 {
    @include relative-font-size(1.25);

    @include media-query($on-laptop) {
      @include relative-font-size(1.125);
    }
  }
}
